<template>
  <div class="body">
    <b-overlay :show="spinnerState" rounded="sm">
      <div class="title"> </div>
      <select-maker
        :propsdata="message"
        @spinnerStart="openSpinner"
      ></select-maker>
      <div class="row">
        <div class="col-md-12">
          <b-button variant="primary" class="right-box" v-on:click="download"
            ></b-button
          >
        </div>
      </div>
      <div>
        <b-table
          sticky-header="72vh"
          responsive
          striped
          hover
          :items="getSelectMaker"
          :fields="fields"
          label-sort-asc=""
          label-sort-desc=""
          label-sort-clear=""
          @row-dblclicked="makerDetails"
        ></b-table>
      </div>
    </b-overlay>
  </div>
</template>

<script>
import SelectMaker from "../../components/select/SelectMaker.vue";
import * as XLSX from "xlsx";
// import { mapGetters } from 'vuex'

export default {
  components: {
    SelectMaker,
  },
  data() {
    return {
      message: "testSelView",
      getSelectMaker: [],
      fields: [
        { key: "makerName", label: "", sortable: true, thClass: "w10" },
        { key: "className", label: "", sortable: true, thClass: "w10" },
        {
          key: "importProduct",
          label: "",
          sortable: true,
          thClass: "w20",
        },
        { key: "process", label: "", sortable: true, thClass: "w15" },
        { key: "sales", label: "", sortable: true, thClass: "w10" },
        {
          key: "makerPerson",
          label: " ",
          sortable: true,
          thClass: "w10",
        },
        {
          key: "makerPhone",
          label: " ",
          sortable: true,
          thClass: "w15",
        },
      ],
      spinnerState: false,
    };
  },
  created() {},
  mounted() {
    this.openSpinner();
  },
  computed: {
    // ...mapGetters('makerStore', [
    //   'getSelectMaker'
    // ]),
    SelectMaker() {
      return this.$store.getters["makerStore/getSelectMaker"];
    },
  },
  watch: {
    //     
    SelectMaker(val) {
      this.getSelectMaker = val;
      this.closeSpinner();
    },
  },
  methods: {
    // 
    download() {
      const workBook = XLSX.utils.book_new();
      const workSheet = XLSX.utils.json_to_sheet(this.getSelectMaker);
      XLSX.utils.book_append_sheet(workBook, workSheet, " ");
      XLSX.writeFile(workBook, " .xlsx");
    },
    //   
    makerDetails(item) {
      this.$store.commit("SET_MAKER", "");

      //         
      this.$store.commit("SET_MAKER", item);
      this.$router.push(`/makerUp/${item.id}`);
    },
    //  
    openSpinner() {
      this.spinnerState = true;
    },
    //  
    closeSpinner() {
      this.spinnerState = false;
    },
  },
};
</script>

<style scoped>
.right-box {
  float: right;
  width: auto;
}
</style>